.left {
    height: 250px;
    width: 350px;
    margin: 20px 20px;
    position: absolute;
    background-color: white;
    border-radius: 5px;
    padding: 30px 30px;
}

.sharelink {
    border: 2px solid #4b53bc;
    background-color: #4b53bc;
    color: white;
    border-radius: 10px;
    font-size: 15px;
    text-decoration: none;
    padding: 7px 10px;
    margin-top: 30px;
    margin-left: 15px;
    font-weight: bold;
    cursor: pointer;
}
.ready{
    text-align: center;
    font-size: 20px;
    // font-weight: bold;
    margin-bottom: 20px;
    color: rgb(0, 0, 0);
}
.close{
    color: white;
    background-color: black;
}


.meet-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #f1f3f5;
    padding: 15px;
    border-radius: 5px;
    height: 30px;
    span {
      font-size: 14px;
      font-weight: 600;
      border: none;
      outline: none;
      background: none;
      flex: 1;
      color: #555;
    }
    .icon {
      cursor: pointer;
      color: #555;
    }
  }