*{
    margin: 0px;
    padding: 0px;
}
body{
    // background-image: url(images/bg.jpg);
    /* background-image: linear-gradient(
    rgba(0,0,0,0.5)
    rgba(0, 0, 0, 0.5); */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    font-size: 20px;
    overflow-x:hidden ;
