}

.row{
    max-width: 1180px;
    margin: 0 auto;
}
.logo{
    height:150px;
    width:auto;
    float:left;
    margin-top:0px;
    margin-left: -40px;
    
}

.lo {
    position: absolute;
    bottom: 6px;
    right: 16px;
    
    height:480px;
    width:auto;
  }
  

.main-content-header{
    width: 1180px;
    position: absolute;
    top: 50%;
    left: 45%;
    transform: translate(-50%,-50%);

}
h1{
    color: #4b53bc;
    font-size: 220%;
